* {
  margin: 0;
  padding: 0;
}

body {
  font-family: Roboto, sans-serif;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

.container, .header {
  width: 100%;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 768px) {
  .container, .header {
    padding: 0 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container, .header {
    padding: 0 32px;
  }
}

.header {
  background-image: url("backgroundMobile.14f69e97.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 40px;
  padding-bottom: 44px;
}

.header--library {
  background-image: url("backgroundLibraryMobile.56bb7943.jpg");
}

.header__container {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 54px;
  display: flex;
}

.header__container--margin {
  margin-bottom: 62px;
}

.header__logo {
  justify-content: center;
  align-items: center;
  display: flex;
}

.header__icon {
  fill: #fff;
}

.header__title {
  color: #fff;
  margin-left: 8px;
  font-size: 1.875rem;
  font-weight: 500;
  line-height: 2.1875rem;
}

.header__list {
  align-items: center;
  gap: 10px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .header__list {
    gap: 40px;
  }
}

.header__link {
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  font-size: .75rem;
  font-weight: 500;
  line-height: .875rem;
  display: block;
}

.header__link--active {
  position: relative;
}

.header__link--active:after {
  content: "";
  width: 100%;
  height: 3px;
  background: #ff001b;
  border-radius: 1px;
  display: block;
  position: absolute;
  top: 14px;
}

.header__logout {
  visibility: hidden;
  text-align: baseline;
  color: #fff;
  background-color: #0000;
  border: none;
  font-size: .6rem;
  font-weight: 500;
  position: absolute;
  top: -29px;
  right: 0;
}

.header__logout:hover {
  color: red;
}

.form {
  width: 280px;
  margin: 0 auto;
  display: block;
  position: relative;
}

@media screen and (min-width: 768px) {
  .form {
    min-width: 336px;
  }
}

@media screen and (min-width: 1280px) {
  .form {
    min-width: 394px;
  }
}

.form__input {
  width: 100%;
  height: 20px;
  color: #fff;
  background-color: #0000;
  border: none;
  border-bottom: 1px solid #fff;
  outline: none;
  margin-bottom: 16px;
  font-size: .875rem;
  line-height: 1rem;
}

.form__input::placeholder {
  color: #fff;
  padding: 2px;
  font-size: .875rem;
  line-height: 1rem;
}

.form__btn {
  background-color: #0000;
  border: none;
  position: absolute;
  top: 4px;
  right: 0;
}

.form__icon {
  fill: #fff;
  align-items: center;
}

.form__result {
  color: #ff001b;
  text-align: left;
  font-size: .75rem;
  line-height: 1rem;
}

.layout__form {
  justify-content: flex-start;
  gap: 20px;
  margin-top: 2px;
  display: flex;
  position: relative;
}

.login__form {
  visibility: hidden;
  flex-flow: column wrap;
  align-content: center;
  margin-bottom: 5px;
  display: flex;
  position: absolute;
  top: 20px;
}

.login__input {
  height: 15px;
  width: 113px;
  color: #fff;
  background-color: #0000;
  border: none;
  border-bottom: 1px solid #fff;
  outline: none;
  font-size: .75rem;
  line-height: 1rem;
}

.login__input::placeholder {
  color: #fff;
  opacity: .5;
  text-align: left;
  font-size: .75rem;
  font-style: italic;
  line-height: 1rem;
}

.login__btn {
  visibility: hidden;
  width: 70px;
  height: 15px;
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #0000;
  border: 1px solid #fff;
  border-radius: 4px;
  margin-top: 3px;
  font-size: .75rem;
  font-weight: 500;
  line-height: .875rem;
  transition: all .3s ease-in-out;
  position: absolute;
  top: 50px;
  left: 20px;
}

.login__btn:hover, .login__btn:focus {
  background-color: #ff6b08;
}

.logged {
  text-transform: none;
  visibility: hidden;
  font-size: .6rem;
  position: absolute;
  top: -30px;
  right: 50px;
}

.button {
  justify-content: center;
  align-items: center;
  gap: 20px;
  display: flex;
}

.button__status {
  width: 130px;
  height: 40px;
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #0000;
  border: 1px solid #fff;
  border-radius: 4px;
  font-size: .75rem;
  font-weight: 500;
  line-height: .875rem;
  transition: all .3s ease-in-out;
}

.button__status:hover, .button__status:focus {
  background-color: #ff6b08;
}

