.loader-wrapper {
  width: 100%;
  height: 100%;
  background-color: #54545490;
  justify-content: center;
  align-items: center;
  display: none;
  position: fixed;
  inset: 0;
}

.loader {
  width: 2.5em;
  height: 2.5em;
  position: relative;
  transform: rotate(165deg);
}

.loader:before, .loader:after {
  content: "";
  width: .5em;
  height: .5em;
  border-radius: .25em;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader:before {
  animation: 2s infinite before;
}

.loader:after {
  animation: 2s infinite after;
}

@keyframes before {
  0% {
    width: .5em;
    box-shadow: 1em -.5em #fff, -1em .5em #fff;
  }

  35% {
    width: 2.5em;
    box-shadow: 0 -.5em #fff, 0 .5em #fff;
  }

  70% {
    width: .5em;
    box-shadow: -1em -.5em #fff, 1em .5em #fff;
  }

  100% {
    box-shadow: 1em -.5em #fff, -1em .5em #fff;
  }
}

@keyframes after {
  0% {
    height: .5em;
    box-shadow: .5em 1em #fff, -.5em -1em #fff;
  }

  35% {
    height: 2.5em;
    box-shadow: .5em 0 #fff, -.5em 0 #fff;
  }

  70% {
    height: .5em;
    box-shadow: .5em -1em #fff, -.5em 1em #fff;
  }

  100% {
    box-shadow: .5em 1em #fff, -.5em -1em #fff;
  }
}

.loader {
  position: absolute;
  top: calc(50% - 1.25em);
  left: calc(50% - 1.25em);
}

